function dropAcordion() {
    var blockBox = $('.process__item');
    $(blockBox).on('click', function () {
        var blockBoxContent = $(this).find('.process__body');
        if ($(this).hasClass('active')) {
            $(this).removeClass('active');
            console.log('1');
        } else {
            $(blockBox).removeClass('active');
            $(this).addClass('active');

            console.log('3');
        }
    })
}

dropAcordion();

function tabVacancy() {
    var tab = $('.table__tab')
    $(tab).on('click', function () {
        $(tab).removeClass('active')
        $(this).addClass('active')
    })
}

tabVacancy()

function table_vacancy() {
    var tabs = $('.table__tab');
    console.log($('[data-vacancy]'))
    $(tabs).on('click', function () {
        var dataAtr = $(this).attr('data-vacancy')
        console.log(dataAtr)
        if (dataAtr === "php") {
            $('.table__body').removeClass('active')
            $('#php').addClass('active')
        }
        if (dataAtr === "Project-manager") {
            console.log('byyyy')
            $('.table__body').removeClass('active')
            $('#project-manager').addClass('active')
        }
        if (dataAtr === "Javascript") {
            console.log('byyyy')
            $('.table__body').removeClass('active')
            $('#JS').addClass('active')
        }
        if (dataAtr === "sales") {
            console.log('byyyy')
            $('.table__body').removeClass('active')
            $('#sales').addClass('active')
        }
        if (dataAtr === "design") {
            console.log('byyyy')
            $('.table__body').removeClass('active')
            $('#design').addClass('active')
        }
    })
}

table_vacancy();

function modal() {
    let btn = $('.hero__btn-link')
    let modal = $('#modal')
    btn.on('click', function () {
        console.log('byyyy')
        modal.show('slow').css('display', 'flex').addClass('active')
        $('body').css('overflow', 'hidden')
        if (modal.hasClass('active')) {
            $('.modal .close').on('click', function () {
                modal.hide('slow')
                $('body').css('overflow', 'auto')
            })
        }
    })
}

modal()

$('.single-item').slick({
    dots: true
});






